.solves {
	display: flex;
	flex-direction: column;

	.solve-heading {
		font-size: 28px;
		font-weight: bold;
		color: $black;
		margin-bottom: 30px;

		.solve-sub-heading {
			font-size: 14px;
			color: $dove-gray;
			font-weight: normal;
			font-style: italic;
		}
	}

	.solve-ctf-heading {
		font-size: 18px;
		font-weight: bold;
	}

	.solve-group {
		display: flex;
		flex-direction: column;
		padding: 0px 0px 20px 0px;

		.solve-boxes {
			display: flex;
		}

		.solve-writeup {
			color: $black;

			.solve-box {
				display: flex;
				flex-direction: column;
				height: 60px;
				min-width: 60px;
				background-color: $white;
				margin: 10px 20px 10px 0px;
				justify-content: center;
    			align-items: center;
    			border: 1px solid $silver;

    			.solve-category {
    				text-transform: uppercase;
    				font-size: 12px;
					margin: 0px 5px;
    			}

    			.solve-points {
    				font-size: 20px;
    				font-weight: bold;
    			}
			}
		}
	}
}

.writeup-container { 
	background-color: white;
	padding: 30px;
	border-left: 6px solid $contrast-color;

	.image-centered {
		display: flex;
		flex-direction: column;
		align-items: center;
		font-size: 12px;
	    font-weight: bold;
	    font-style: italic;

		img {
			max-width: 90vw;
		}
	}

	h2 {
		margin-bottom: 1rem;
    	color: #52c0b0;
	}

	pre {
		background-color: $wild-sand !important;
		padding: 10px !important;	
	    overflow: hidden !important;
	    overflow-x: scroll !important;
	}

	code {
		background-color: #fff1f3;
    	padding: 2px;
	}
}